import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const generateNotaPDF = (tickets, from, tanggal, noPembayaran, supplier) => {
  // initialize jsPDF
  const doc = new jsPDF();

  // define the columns we want and their titles
  const tableColumn = [
    "No",
    "Jenis Pembayaran",
    "Gross Bayar",
    "Harga",
    "Kadar",
    "Netto",
    "Pkg",
    "Gw + Pkg",
  ];
  // define an empty array of rows
  const tableRows = [];

  // for each ticket pass all its data into an array
  tickets.forEach((ticket, i) => {
    const ticketData = [
      i,
      ticket.jenis,
      ticket.gross,
      ticket.harga,
      ticket.kadar,
      ticket.netto,
      ticket.pkg,
      ticket.gw,
      ticket.gwpkg,
    ];
    // push each tickcet's info into a row
    tableRows.push(ticketData);
  });

  // startY is basically margin-top
  doc.autoTable(tableColumn, tableRows, { startY: 40 });
  let finalY = doc.lastAutoTable.finalY + 10;
  const date = Date().split(" ");
  // we use a date string to generate our filename.
  const dateStr = date[0] + date[1] + date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  doc.text(from, 14, 15);
  doc.setFontSize(10);
  //row 1
  doc.text("Tanggal", 14, 25);
  doc.text(":", 37, 25);
  doc.text(tanggal, 40, 25);
  doc.text("No Pembayaran", 120, 25);
  doc.text(":", 151, 25);
  doc.text(noPembayaran, 155, 25);
  //row 2
  doc.text("Supplier", 14, 30);
  doc.text(":", 37, 30);
  doc.text(supplier, 40, 30);
  // Diterima
  doc.text("Tanda Terima : _________________ ", 14, finalY);

  //Stamp
  doc.text("User", 160, finalY + 10);
  doc.text(":", 168, finalY + 10);
  doc.text("NAGATECH", 170, finalY + 10);
  doc.text("Cetak", 160, finalY + 15);
  doc.text(":", 168, finalY + 15);
  doc.text("17/11/2020", 170, finalY + 15);
  doc.text("Valid", 160, finalY + 20);
  doc.text(":", 168, finalY + 20);
  doc.text("@OCTA", 170, finalY + 20);

  doc.save(`report_${dateStr}.pdf`);
};

export default generateNotaPDF;
